.production {
  box-sizing: border-box;
  padding: 6.6rem 0 8.9rem;

  &__list {
    list-style: none;
    margin: 5.4rem 0 0;
    padding: 0;
  }

  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 10% 0 0;

    p {
      margin: 0;
      font-family: $Vafle;
      font-size: 1.5rem;
      line-height: 133%;
      width: 60%;
    }
  }

  &__item-img {
    min-width: 7.625rem;
    max-width: 7.625rem;
    height: 7.625rem;
    margin: 0 10% 0 0;
  }
}

@include media-breakpoint-down(xxl) {
  .production {
    padding: 3rem 2rem;
    margin: 0;

    &__list {
      margin: 3rem 0 0;
    }

    &__item {
      padding: 0;
      margin: 0 0 1rem;

      &::before {
        margin: 0 6.8% 0 0;
        min-width: 80px;
        height: 80px;
        background-size: contain;
      }

      p {
        font-size: 1.2rem;
        width: 100%;
      }

      &--cargo {
        p {
          word-wrap: break-word;
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .production {
    padding: 3rem 1rem;
  }
}
